body {
  background-image: url("bckg.ed5d6b17.jpg");
  background-repeat: repeat;
}

.text {
  background-color: #fff;
  border: 5px solid #daa520;
  border-radius: 20px;
  padding: 8px;
  font-family: Rubik, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.6;
}

.change-choice-link {
  color: inherit;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  text-decoration: none;
  display: flex;
  position: absolute;
  top: 10px;
  left: 10px;
}

.change-choice-link .arrow-svg {
  padding-right: 10px;
}

.change-choice-link:hover {
  color: #daa520;
}

.change-choice-link:hover .arrow-svg {
  fill: #daa520;
}

.main-choice {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.main-choice a {
  min-width: 500px;
  height: 333px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-clip: content-box;
  border: 5px solid #daa520;
  border-radius: 20px;
  transition: transform .2s ease-in-out, border-color .2s ease-in-out;
  transform: scale(1);
}

.main-choice a.cat {
  background-image: url("cat.daaa6732.jpg");
}

.main-choice a.dog {
  background-image: url("dog.9def4028.jpg");
}

.main-choice a:hover {
  transform: scale(1.05);
}

.main-choice a.cat:hover {
  border-color: red;
}

.main-choice a.dog:hover {
  border-color: #228b22;
}

.header {
  width: 900px;
  text-align: center;
  margin: 50px auto;
  padding: 20px;
  font-size: 50px;
  font-weight: 500;
  line-height: 1;
}

.selector {
  display: inline-block;
}

.label-select {
  border: none;
  font-size: 25px;
}

.breed-select {
  border-radius: 15px;
  margin: 5px 10px;
  padding: 5px 10px;
  display: inline-block;
  box-shadow: inset 0 0 5px 1px rgba(0, 0, 0, .5);
}

#breed {
  min-width: 150px;
  border: none;
  margin: 0;
  padding: 0;
  font-size: 25px;
}

#breed:focus {
  outline: none;
}

.header.choice {
  width: fit-content;
}

.temperament-header {
  font-weight: 700;
}

.breed-info {
  max-width: 1000px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.breed-info-right {
  padding: 0 20px;
}

.breed-name {
  margin: 0;
  font-size: 50px;
  font-weight: 500;
  line-height: 1;
}

.animal-img {
  max-width: 100%;
  object-fit: contain;
  align-self: start;
  display: block;
}

.temper {
  font-style: italic;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #b98710;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid #a9a9a9;
  border-color: #a9a9a9 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  width: 500px;
  color: #c40202;
  margin-left: auto;
  margin-right: auto;
}

